import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import StudentData from '@/components/StudentData'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/students',
      name: 'StudentData',
      component: StudentData    }
  ]
})
